*,::before,::after{margin:0;padding:0;border:0;box-sizing:border-box;font:inherit;vertical-align:baseline}ol,ul{list-style:none}table{border-collapse:collapse;border-spacing:0}td,th{padding:0}a{text-decoration:none}a:active,a:hover{outline:0}a{color:rgba(0,0,0,0.690196)}input[type='text'],input[type='number']{height:38px;padding:6px 10px;background-color:#fff;border:1px solid #d1d1d1;border-radius:4px;box-shadow:none;box-sizing:border-box}textarea{min-height:65px;padding-top:6px;padding-bottom:6px}input[type='email']:focus,input[type='number']:focus,input[type='search']:focus,input[type='text']:focus,input[type='tel']:focus,input[type='url']:focus,input[type='password']:focus,textarea:focus,select:focus{border:1px solid #f6e995;outline:0}label,legend{display:block;margin-bottom:0.5rem;font-weight:600}fieldset{padding:0;border-width:0}input[type='checkbox'],input[type='radio']{display:inline}label>.label-body{display:inline-block;margin-left:0.5rem;font-weight:normal}button,input[type='submit']{display:inline-block;height:38px;padding:0 30px;text-align:center;font-size:11px;font-weight:600;line-height:38px;letter-spacing:0.1rem;text-transform:uppercase;text-decoration:none;white-space:nowrap;background-color:transparent;border-radius:4px;border:1px solid #bbb;cursor:pointer;box-sizing:border-box;color:rgba(26,26,26,0.690196);background-color:#f0db4f;border-color:#ecd120;outline:none}button:hover,input[type='submit']:hover{color:#fff;border-color:#dfc513}button:active,input[type='submit']:active{background-color:#dfc513}html{font-size:62.5%}body{font-family:'Raleway', sans-serif;font-size:1.5rem;line-height:1.6;font-weight:400;color:rgba(0,0,0,0.690196);font-family:'Raleway', 'HelveticaNeue', 'Helvetica Neue', Helvetica, Arial, sans-serif}h1,h2,h3,h4,h5,h6{margin-top:0}h1{font-size:3.6rem;line-height:1.2;letter-spacing:-0.1rem}h2{font-size:3.2rem;line-height:1.25;letter-spacing:-0.1rem}h3{font-size:3rem;line-height:1.3;letter-spacing:-0.1rem}h4{font-size:2.4rem;line-height:1.35;letter-spacing:-0.08rem}h5{font-size:1.8rem;line-height:1.5;letter-spacing:-0.05rem}h6{font-size:1.5rem;line-height:1.6;letter-spacing:0}@media (min-width: 550px){h1{font-size:5.0rem}h2{font-size:4.2rem}h3{font-size:3.6rem}h4{font-size:3.0rem}h5{font-size:2.4rem}h6{font-size:1.5rem}}p{line-height:2rem}.App{min-height:100vh;display:grid;grid-template-rows:auto 1fr;-webkit-align-items:center;align-items:center}.menu-permanente{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;grid-gap:5px;gap:5px;box-shadow:rgba(0,0,0,0.15) 0px 0px 2px 1px;padding:0.6rem}.menu-permanente__titulo{-webkit-user-select:none;-ms-user-select:none;user-select:none;font-size:2.6rem}.menu-permanente__btn-menu{border:none;background-color:white;-webkit-user-select:none;-ms-user-select:none;user-select:none;padding:0 0;transition:-webkit-transform 250ms ease-in;transition:transform 250ms ease-in;transition:transform 250ms ease-in, -webkit-transform 250ms ease-in}.menu-permanente__btn-menu:hover{color:#434646;-webkit-transform:scale(1.1);transform:scale(1.1)}.menu-flutuante{position:fixed;width:70%;max-width:300px;height:100vh;background-color:#fff;box-shadow:rgba(0,0,0,0.15) 0px 0px 2px 1px;top:0;left:-100%;transition:450ms}.menu-flutuante__btn-fechar{border:none;padding:0;background-color:white;-webkit-user-select:none;-ms-user-select:none;user-select:none;position:absolute;right:0}.menu-flutuante__link{width:100%;display:-webkit-flex;display:flex}.menu-flutuante__item{border-bottom:1px solid #bbb;display:-webkit-flex;display:flex;-webkit-align-items:flex-end;align-items:flex-end;cursor:pointer;margin-bottom:3px}.menu-flutuante__item:hover{background-color:#66666618}.menu-flutuante__img{max-width:75px;vertical-align:middle}.menu-flutuante__texto{margin-left:0.4rem;-webkit-user-select:none;-ms-user-select:none;user-select:none;-webkit-flex:1 1;flex:1 1;-webkit-align-self:flex-end;align-self:flex-end}.isactive{left:0;transition:250ms}@media (min-width: 1800px){.menu-permanente__titulo{font-size:4rem}.menu-flutuante{max-width:350px}.menu-flutuante__texto{font-size:2.2rem}}.center-card{width:90%;min-width:350px;max-width:1024px;margin:0 auto;padding:2rem 2rem;box-shadow:rgba(0,0,0,0.15) 0px 0px 3px 1px}.center-card__title{text-align:center;font-weight:lighter;margin-bottom:1rem}.center-card__question-title{padding-bottom:0.8rem}.center-card__answer{margin-top:1rem;padding:0.5rem;min-height:300px;line-height:1.5rem;border:1px solid #d1d1d1;border-radius:4px;-webkit-user-select:none;-ms-user-select:none;user-select:none}.center-card__footer{padding:0.6rem;text-align:center}@media (min-width: 1100px){.center-card__answer{min-height:50vh}}

